:root {
  --green: #00bc87;
  --light-green: #bdffe9;
  --blue: #00c2ee;
  --light-blue: #c0f6ff;
  --yellow: #ffa800;
  --light-yellow: #ffe7bd;
  --red: #d00090;
  --light-red: #ffbdee;
  --orange: #e74c3c;
  --light-orange: #ffc0bd;

  --purple: #5849b5;
  --purple-light: #dfdaff;
  /* Primary */
  --primary: #282828;
  --secondary: #333333;

  --light-0: #fafafa;
  --light-1: #f3f3f4;
  --light-2: #e6e6e6;
  --light-3: #585858;
  --light-4: #b9b9b9;

  --pink: #ea4c89;
  --pink-light: #ffe6ef;
}

/* Focusing the button with a keyboard will show a dashed black line. */
a {
  color: var(--primary);
}

a:hover {
  color: var(--light-3);
  text-decoration: none;
}
button:focus {
  outline: none;
}

input,
optgroup,
select,
textarea:focus {
  outline: none;
}

html,
body {
  font-family: "Space Grotesk";
  overflow-x: hidden;
  background: var(--light);
}

p {
  margin: 0;
}

.header {
  padding: 30px;
  padding-top: 10px;
  padding-bottom: 0px;
}

.small {
  font-size: 0.8rem;
  line-height: 18px;
  margin-top: 20px;
}

.h1 {
  font-family: "Poppins";
  font-size: 40px;
  padding-top: 20px;
  font-weight: 700;
  animation: 3s changecolors infinite;
}

.h1 i {
  display: none;
}

/* main image container */
.main-container {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 28%;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1;
  transition: 1s;
}

.image-container {
  width: 400px;
  height: 400px;
  display: flex;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  justify-content: center;
  align-items: center;
  position: relative;
}

.image-container .image {
  width: 100%;
  height: 100%;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}

.image-container .image-text {
  position: absolute;
  font-family: "Poppins";
  font-size: 120px;
  font-weight: 400;
  line-height: 120px;
}

/* controls */
.controls {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 72%;
  right: 0;
  width: 28%;
  background: white;
  padding-top: 20px;
  padding-left: 25px;
  padding-right: 25px;
  overflow-y: auto;
  z-index: 3;
  border-top-left-radius: 25px;
  border-bottom-left-radius: 25px;
  box-shadow: 0 0 20px 0 var(--light-2);
  transition: 0.5s;
}

.controls::-webkit-scrollbar {
  width: 14px;
  background: var(--light-1);
}

.controls::-webkit-scrollbar-thumb {
  background-color: var(--light-3);
  border-radius: 100px;
  border: solid 3px var(--light-1);
}

.controls::-webkit-scrollbar-corner {
  background-color: transparent;
}

.controls .h2 {
  font-weight: 800;
  font-family: "Poppins";
  margin-bottom: 0;
  font-size: 30px;
}

.dot {
  animation: 3s changecolors infinite;
}

.badge {
  font-size: 11px;
  font-weight: 500;
  background: var(--pink);
  color: white;
}

.controls .desc {
  font-weight: 400;
  font-family: "Inter";
  margin-bottom: 0;
  font-size: 12px;
  margin-top: 5px;
}

.title {
  font-size: 13px;
  font-weight: 700;
}

.label {
  font-size: 12px;
  font-weight: 700;
  font-family: "Inter";
  color: var(--light-3);
}

input {
  font-family: "Inter";
  border: solid 1px var(--light-2);
  font-weight: 600;
  border-radius: 10px;
  font-size: 15px;
  padding: 10px;
  padding-left: 15px;
  font-size: 12.5px;
  height: 35px;
  width: calc(100% - 0px);
  margin-top: 2px;
}

input[type="number"] {
  background: var(--light-1);
  color: var(--primary);
}

input[type="text"] {
  background: var(--light-1);
  color: var(--primary);
  width: 100%;
}

.image-select-btn {
  color: white;
  background: var(--purple);
  text-align: center;
  cursor: pointer;
  font-family: "Heebo";
  letter-spacing: 0.5px;
  border: none;
  font-weight: 600;
  border-radius: 10px;
  font-size: 13px;
  padding: 10px;
  height: 40px;
  width: calc(100% - 10px);

  margin-top: 5px;
  margin-left: 5px;
}

.image-select-btn:hover {
  background: var(--purple-light);
  color: var(--purple);
}

.image-select-btn:hover > i {
  animation: 800ms bounce 1;
}

@keyframes bounce {
  0% {
    transform: translateY(7px);
  }
  25% {
    transform: translateY(-7px);
  }
  100% {
    transform: translateY(0px);
  }
}

.add-btn {
  border: none;
  background: var(--purple);
  color: white;
  height: 35px;
  width: 35px;
  border-radius: 10px;
  margin-top: 5px;
}

.ver {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.ver-top {
  display: flex;
  justify-content: space-between;
  align-items: top;
}

.ver-top > div {
  flex: 1 1 auto;
  border: 5px white solid;
}

.bold {
  font-weight: 700;
}

@keyframes changecolors {
  0% {
    color: var(--yellow);
  }
  20% {
    color: var(--red);
    translate-x: -20px;
  }
  40% {
    color: var(--green);
  }
  60% {
    color: var(--blue);
  }
  80% {
    color: var(--orange);
  }
  100% {
    color: var(--yellow);
  }
}

/* images */
.images {
  padding-left: 6px;
  padding-right: 6px;

  margin-top: 5px;
  border-radius: 10px;
  font-family: "Inter";
}

.images-container {
  margin-top: 5px;
  padding: 10px;
  border-radius: 10px;
  font-family: "Inter";
  display: grid;
  grid-template-columns: auto auto;
  grid-column-gap: 10px;
  grid-row-gap: 10px;
  background: var(--light-1);
}

.images .title {
  font-size: 20px;
}

.images .img {
  width: 100%;
  height: 140px;
  background-size: cover;
  background-position: center;
  padding-left: 10px;
  padding-top: 10px;
  padding-bottom: 15px;
  border-radius: 5px;
  position: relative;
  border: solid 1.5px var(--light-2);
}

.images .img .edit {
  border: none;
  height: 30px;
  width: 30px;
  border-radius: 50px;
  background: rgba(0, 0, 0, 0.548);
  color: white;
  transition: 0.2s;
}

.images .img .edit:hover {
  opacity: 0.8;
}

.images .text-second {
  font-size: 13px;
  padding-left: 10px;
  border-radius: 5px;
  font-weight: 700;
  background: var(--light);
  height: 25px;
  font-size: 13px;
  width: calc(100% - 9px);
  position: absolute;
  left: 5px;
  right: 15px;
  bottom: 6px;
  border: solid 1.5px var(--light-2);
}

.images .img .inner {
  padding-top: 10px;
  padding-left: 15px;
  padding-right: 15px;
  padding-bottom: 15px;
  border-top-left-radius: 15px;
  border-bottom-left-radius: 15px;
  background: rgb(255, 255, 255);
  width: 345px;
  border-top:solid 1px rgb(217, 217, 224);
  border-left:solid 1px rgb(217, 217, 224);
  border-bottom:solid 1px rgb(217, 217, 224);
  position: fixed;
  bottom: 0;
  right: 28%;
  z-index: 5;
}

.images .img .inner .editing-img-small {
  background-size: cover;
  background-position: center;
  width: 40px;
  height: 40px;
}

.images .img small {
  font-size: 11px;
  font-weight: 600;
}

.slider {
  margin-top: 15px;
  margin-bottom: 40px;
  width: 100%;
  font-weight: 600;
}

.slider-mark {
  background: var(--pink);
  color: white;
  width: 6px;
  height: 6px;
  border-radius: 100%;
}

.slider-thumb {
  background: var(--pink);
  color: white;
  width: 25px;
  height: 25px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 10px;
  border-radius: 50px;
  font-weight: 500;
  cursor: pointer;
  margin-top: -10px;
  letter-spacing: 0.5px;
}

.slider-thumb:focus {
  border: none;
  outline: none;
  cursor: grabbing;
  box-shadow: 0 0 30px 0 var(--pink-light);
}

.slider-track {
  background: var(--light-2);
  height: 6px;
  border-radius: 50px;
  border: none;
  margin: 0;
}

.images .text {
  font-size: 13px;
  padding-left: 10px;
  border-radius: 5px;
  font-weight: 700;
  background: var(--light);
  height: 25px;
  font-size: 13px;
  width: 100%;
  margin-bottom: 5px;
  border: solid 1.5px var(--light-4);
}

.close-btn {
  border: none;
  width: 30px;
  height: 30px;
  background: none;
  font-size: 13px;
}

.play-btn {
  height: 40px;
  width: 40px;

  border: none;
  border-radius: 50px;
  font-family: "Inter";
  font-size: 12px;
  background: var(--light-2);
  color: var(--light-3);
  font-weight: 700;
  transition: 0.3s;
}

.play-btn:hover {
  color: var(--primary);
}

.clear-btn {
  height: 35px;
  width: 35px;

  border: none;
  border-radius: 50px;
  font-family: "Inter";
  font-size: 12px;
  background: var(--pink);
  color: white;
  font-weight: 700;
  transition: 0.3s;
}

.clear-btn:hover {
  background: var(--pink-light);
  color: var(--pink);
}

.remove-btn {
  height: 30px;
  width: 100%;
  border: none;
  border-radius: 10px;
  font-family: "Inter";
  background: var(--pink);
  color: white;
  font-weight: 600;
  transition: 0.3s;
  font-size: 11px;
  margin-top: 15px;
}

.remove-btn:hover {
  background: var(--pink-light);
  color: var(--pink);
}

/* end of images */

footer {
  margin-top: 20px;
  margin-bottom: 20px;
  color: black;
  font-family: "Poppins";
}

footer img {
  border-radius: 100%;
  margin-top: -3px;
}

@media (max-width: 700px) {
  .pc {
    display: none;
  }

  .main-container {
    display: none;
  }

  .controls {
    display: none;
  }
}
@media (min-width: 700px) {
  .mobile {
    display: none;
    padding-left: 20px;
    padding-right: 20px;
  }
}

.showControls {
  position: fixed;
  top: 20px;
  right: 25px;
  z-index: 7;
  background: var(--light-2);
  color: var(--secondary);
  height: 40px;
  width: 40px;
  border: none;
  border-radius: 50px;
  font-family: "Poppins";
  font-size: 13.5px;
  font-weight: 900;
  transition: 0.3s;
}

.showControls.show {
  background: var(--pink);
  color: white;
}

.showControls:hover {
  color: var(--dark);
  background: var(--light);
  box-shadow: 0 0 15px 0 white;
}


.fixed-btn {
  position: fixed;
  top: 70px;
  right: 25px;
  z-index: 1;
  background: var(--purple);
  color: white;
  transition: 0.5s;
}

.fixed-btn:hover{
  color: var(--purple);
  background: var(--purple-light);
  box-shadow: 0 0 15px 0 white;
}